@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
*{
  margin:0;
  padding:0;
  border:0;
  box-sizing:border-box;
}

body{
  font-family: 'Roboto', sans-serif;
}

.container{
  max-width:800px;
  margin:0 auto;

}

h1,h3{
  color:#FFFFFF;
}
#informacion{
  display:flex;
  flex-direction:row;
}

p{
  text-align:justify;
}




.errorxddx{
    text-align: center;
    
}


.home{
    
    width: 100%;
    
    height: 100vh;
   
}

.home__menu{
    margin-bottom: 35px;
    display: flex;
    justify-content: space-around;
}

.home_menu_item{
    display: flex;
    margin :0px 5px;
    padding:5px;
}
.home_menu_item li{
  
    font-size: 24px;
    margin-left: 10px;
    margin-right: 10px;
    color:black;
    font-weight:bold;
    list-style: none;

}
.home_menu_item li:hover{
  
    
    text-decoration: none;
    color:black;
    cursor: pointer;
   
}
.seccion{
    display: flex;
    justify-content: space-evenly;
    align-items:center;
}
.seccion__buttonhome{
    margin-top: 10px;
    outline: none;
    width: 100px;
    background-color:orange;
    color: #fff;
    height: 40px;
    border-radius: 10px;
    border:none;
    cursor: pointer;
        
}

button:hover{
     background-color:FAFA75;
    cursor: pointer;
}
h1{
    color:#FAFA75;
    font-size: 45px;
    font-weight: bold;
}
.seccion__content{
    flex:1 1;
    
}
h2{
    color:orange;
    font-size: 30px;
    font-weight: bold;

}

*{
    margin:0;
    padding:0;
    border:0;
    box-sizing:border-box;
  }
.seccion__button{
    margin-top: 10px;
    outline: none;
    width: 50px;
    background-color:orange;
    color: #fff;
    height: 40px;
    border-radius: 10px;
    border:none;
    cursor: pointer;
    position: absolute; right: 90%;
    position: absolute; top: 1%;
   
}

button:hover{
     background-color:#FAFA75;
    cursor: pointer;
}
.container{
    max-width:800px;
    margin:0 auto;
  
  }
  @media only screen and (max-width: 1000px){
    #bseccion__button {
       font-size: 10px;
       padding: 12px;
     }
   }
header{
    background-image:url(data:image/png;base64,);
    background-repeat:no-repeat;
    display:flex;
    padding:50px;
    max-height:300px;

}

.foto{
    flex-basis:50%;
    text-align:center;
}

.foto img{
    max-width:150px;
    border-radius:20%;
    border:3px solid orange;
}

.texto{
    flex-basis:60%;
}

.texto h1{
    color: black;
}
.texto h3{
    font-weight:200;
    color: black;

    
}

.texto h2{
    text-align: left; 
}

.nombre{
    color: orange;
}
.contacto{
    flex-basis:50%;
    padding-left:20px;
}

.info{
    margin-bottom:5px;
}

.info i{
    background-color:#0BAFE3;
    padding:10px;
    border-radius:50%;
    margin-right:10px;
    font-size:20px;
}

.ref i{
    font-size:2em;
    color: #274257;
    padding:10px;
}

.refnombre{
    font-weight:500;
}

.enlaces img{
    max-width:150px;
}

h2{
    text-align:center;
}

hr{
    border-bottom:4px solid orange;
    width:55%;
    margin:5px auto;
}

.acerca{
    flex-basis:50%;
}

.infolab{
    padding:5px 10px;
}

.titlab{
    background-color:#FAFA75;
    padding:5px 10px;
    color: orange;
    margin:5px 0;
}

.estudio p{
    padding-left:40px;
    margin:10px 0;
}

progress[value]{
    width:100%;
    height:10px;
    -webkit-appearance:none;
            appearance:none;
    color:#FAFA75;
}

h4{
    color:orange;
    font-size: 20px;
    font-weight: bold;
}



progress::-moz-progress-bar {background-color: #FAFA75;}
progress::-webkit-progress-bar {background-color:#FAFA75;}

.amongus{
    min-height:130px;
    background-repeat:no-repeat;
    display:flex;
    margin: 0 auto;
}
